.Contact {
	background: #f46b45; /* fallback for old browsers */
	background: -webkit-gradient(linear, left top, left bottom, from(#eea849), to(#f46b45));
	background: -webkit-linear-gradient(top, #eea849, #f46b45);
	background: -o-linear-gradient(top, #eea849, #f46b45);
	background: linear-gradient(to bottom, #eea849, #f46b45);
	min-height: 100vh;
	padding: 30px;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
}

/* Just a wrapper for all category cards */
.Contact__cards__wrapper {
	font-family: "Titillium Web", cursive;
	color: rgb(34, 34, 33);
	text-align: center;
}

.Contact__cards__wrapper_intro {
	font-size: 1.4em;
	max-width: 800px;
	text-align: justify;
	font-style: italic;
	color: #22323f;
	line-height: 1.2em;
}

.Contact__Icons {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-line-pack: center;
	    align-content: center;
}

/* Each Contact Link & Icon */
.Contact a {
	color: rgb(252, 216, 209);
	font-size: 3rem;
	margin: 15px;
	-ms-flex: 0 1;
	    flex: 0 1;
}

.Contact a:hover {
	color: rgb(82 15 39);
}



