.SkillCard {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1;
      flex: 1 1;
  -ms-flex-preferred-size: 150px;
      flex-basis: 150px;
  -ms-flex-align: center;
      align-items: center;
  margin: 10px;
}

/* Logo */
.SkillCard svg {
  width: 100%;
  max-width: 33px;
  min-width: 10px;
}

/* Forcing all svg path to have same fill color */
.SkillCard svg path {
  fill: black;
}

/* Description */
.SkillCard span {
  padding-left: 10px;
  font-size: 1.04rem;
}

@media screen and (max-width: 736px) {
  /* Smalling Logo Size */
  .SkillCard svg {
    width: 100%;
    max-width: 25px;
  }

  /* Use when needed in two or three rows */
  /* .SkillCard {
    flex-basis: 100px;
  } */
}



