/* Custom Scroll Bar Styles For Web Kit Browsers */
::-webkit-scrollbar {
	width: 5px;
	background-color: #f5f5f538;
}

::-webkit-scrollbar-track {
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	        box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	border-radius: 10px;
	background-color: #f5f5f538;
}

::-webkit-scrollbar-thumb {
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	        box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
	background-color: #d62929;
}

/* Global Styles for Sections */
.section__header {
	font-family: "Fjalla One", sans-serif;
	font-size: 4rem;
	color: white;
	text-align: center;
	letter-spacing: 3px;
	text-shadow: 4px 3px 0px rgb(43, 40, 40), 9px 8px 0px rgba(0, 0, 0, 0.15);
}

body {
	overflow-x: hidden;
	background: #2e2c2c;
}

.globalLayoutWrapper {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
}

.globalLayoutWrapper > div {
	max-width: 1920px;
}



