.Header {
	display: -ms-flexbox;
	display: flex;
	position: fixed;
	width: 100%;
	z-index: 2;
}

.Header__mainmenu {
	background: #1565c0;
	border-radius: 0 0 10px 10px;
	margin: 0 auto;
}

/* ---------------------------- Start of ".basicMenu" Styles ----------------------------
 * These style get applied for menu in both mainmenu and mobilemenu . (Overrides are below this)
 */

.basicMenu {
	display: -ms-flexbox;
	display: flex;
	list-style-type: none;
	/* Resetting default margin and padding */
	margin: 0;
	padding: 0;
}

/* Resetting Anchor Tags default styles and List Styles*/
.basicMenu li a {
	text-decoration: none;
	color: #e7e0de;

	font-family: "Fjalla One", sans-serif;
	letter-spacing: 0.2rem;
	padding: 15px;
	display: inline-block;
	-webkit-transition: all 500ms ease;
	-o-transition: all 500ms ease;
	transition: all 500ms ease;
	cursor: pointer;
	position: relative;
	z-index: 1;
}

.basicMenu li a:hover {
	/* background: #ef5350; */
	background: #00d3ad;
	color: black;
}

/* Small Arrow Style for active page */
.basicMenu-active:after {
	content: " ";
	position: absolute;
	margin: 0 auto;
	bottom: 0;
	right: 0;
	/* Render Arrow using boders */
	border-style: solid;
	border-width: 0 0 18px 18px;
	border-color: transparent transparent #ffeeca transparent;
	z-index: -1;
}

/* Styling for fontAwesome Icons */
.basicMenu li a svg {
	padding-right: 5px;
}

/* ---------------------------- Overriding Styles for "Header__mainmenu" ----------------------------*/

/* Making first and last list item has same border radius as "Header__mainmenu" to ensure that
  when hovering in list items it shown correctly.
*/
.Header__mainmenu ul li:first-child a {
	border-radius: 0 0 0 10px;
}
.Header__mainmenu ul li:last-child a {
	border-radius: 0 0 10px 0;
}

/* ---------------------------- Overriding Styles for "Header__mobilemenu" ----------------------------*/

.Header__mobilemenu ul li a {
	width: 100%;
}

/* ---------------------------- Media Quieries ----------------------------*/

@media screen and (max-width: 736px) {
	/* Hiding mainmenu, So in mobile devices only mobilemenu available. */
	.Header__mainmenu {
		display: none;
	}
}

@media screen and (min-width: 736px) {
	/* Hiding mobilemenu, So in large devices only mainmenu available. */
	.Header__mobilemenu {
		display: none;
	}
}



